//import React from 'react';

import * as mqtt from "react-paho-mqtt";
import React, { useState, Fragment } from "react";

function App() {
  const [client, setClient] = React.useState(null);
  const _topic = ["device/50HzData"];
  const _options = {};

  React.useEffect(() => {
    _init();
    //_onSubscribe();
  }, []);

  const _init = () => {
    const c = mqtt.connect(
      "test.mosquitto.org",
      Number(8080),
      "mqtt",
      _onConnectionLost,
      _onMessageArrived
    ); // mqtt.connect(host, port, clientId, _onConnectionLost, _onMessageArrived)
    setClient(c);
  };

  // called when sending payload
  const _sendPayload = () => {
    const payload = mqtt.parsePayload("Hello", "World"); // topic, payload
    client.send(payload);
  };

  // called when client lost connection
  const _onConnectionLost = (responseObject) => {
    if (responseObject.errorCode !== 0) {
      console.log("onConnectionLost: " + responseObject.errorMessage);
    }
  };

  const [msg, setMsg] = useState(
    <Fragment>
      {" "}
      <em>...</em>{" "}
    </Fragment>
  );

  // called when messages arrived
  const _onMessageArrived = (message) => {
    console.log("onMessageArrived: " + message.payloadString);
    var note = message.payloadString.toString(); // Updates React state with message
    var jsonval = JSON.parse(note);

    console.log(jsonval.Hz.toString());
    setMsg(jsonval.Hz.toString());
  };

  // called when subscribing topic(s)
  const _onSubscribe = () => {
    client.connect({
      onSuccess: () => {
        console.log("Connected");
        for (var i = 0; i < _topic.length; i++) {
          client.subscribe(_topic[i], _options);
        }
      },
      onFailure: () => {
        console.log("Failed");
      }
    }); // called when the client connects
  };

  // called when subscribing topic(s)
  const _onUnsubscribe = () => {
    for (var i = 0; i < _topic.length; i++) {
      client.unsubscribe(_topic[i], _options);
    }
  };

  // called when disconnecting the client
  const _onDisconnect = () => {
    client.disconnect();
  };

  return (
    <div className="App">
      {" "}
      <header className="App-header">
        <h1>Grid Watch</h1>
        <p>Grid Frequency is: {msg}Hz</p>
        <button style={{ color: "white" }} onClick={_onSubscribe}>
          <h1>Start</h1>
        </button>
      </header>
    </div>
  );
}

export default App;
